.main-navigation-list {
  position: relative;
}

.main-navigation-list__item {
  display: inline-block;
}

.main-navigation-list__anchor {
  color: #000;
  padding: 0 5px;
  font-weight: 700;
  text-decoration: underline;
  text-transform: capitalize;
  @media screen and (min-width: 320px)  {
    text-decoration: none;
    text-transform: uppercase;
  }
}

.header__navigation--general {
  #search,
  #profiles,
  #menu  {
    a {
      width: 15px;
      @media screen and (min-width: 320px) {
        position: absolute;
        text-indent: -99em;
        top: 20%;
      }
    }
  }

  #menu > .main-navigation-list__anchor {
    @media screen and (min-width: 320px) {
      background:  url('/images/icons/menu.svg') no-repeat 0 0 / auto 100%;
      left: 70px;
    }
  }

  #search > .main-navigation-list__anchor {
    @media screen and (min-width: 320px) {
      background:  url('/images/icons/search.svg') no-repeat 0 0 / auto 100%;
      left: 35px;
    }
  }

  #profiles > .main-navigation-list__anchor {
    @media screen and (min-width: 320px) {
      background:  url('/images/icons/user.svg') no-repeat 0 0 / auto 100%;
    }
  }
}
